.header {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.12);
  height: 64px;
  width: 100%;
  position: fixed;
  background-color: #fff;
  z-index: 99;
}
.header .navbar {
  cursor: pointer;
  width: 100%;
  display: flex;
  align-items: center;
}
.header .navbar .right {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}
.header .navbar .right .navbar__logo {
  margin-left: 15px;
  cursor: context-menu;
}
.header .navbar .right .navbar__logo img {
  width: 45px;
}
.header .navbar .right .navbar__location {
  display: flex;
  gap: 13px;
  margin: 0 28px;
}
.header .navbar .right .navbar__Grouping {
  padding: 7px 0;
  margin-left: 10px;
  border-radius: 2px;
  width: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  gap: 13px;
}
.header .navbar .right .navbar__Grouping .icon__down {
  font-size: 12px;
}
.header .navbar .right .navbar__Grouping:hover {
  background-color: red;
}
.header .navbar .right .navbar__Search {
  position: relative;
}
.header .navbar .right .navbar__Search .icon__search {
  position: absolute;
  top: 14px;
  right: 10px;
  font-size: 13px;
  color: gray;
}
.header .navbar .right .navbar__Search .search {
  padding: 10px 40px 10px 0;
  width: 36vw;
  outline: 0;
  border: 0;
  background-color: lightgray;
  border-radius: 3px;
}
.header .navbar .left {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 27px;
}
.header .navbar .left .navbar__btn button {
  padding: 10px 23px;
  border: 0;
  border-radius: 3px;
  background-color: red;
  color: #fff;
  font-weight: 600;
  font-size: 16px;
}
.header .navbar .left .navbar__chat {
  display: flex;
  gap: 10px;
  margin: 0 15px;
}
.header .navbar .left .navbar__profile {
  display: flex;
  gap: 10px;
}